@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.album--artist-name-fit {
    white-space: nowrap; 
    overflow: hidden;
    text-overflow:ellipsis;
}

.album--card {
    width: 120px;
    margin-right: 20px;
    margin-top: 20px;
    background: white;
    border-radius: 15px;
}

@media only screen and (min-width: 401px) { /* 769 */
    .album--card {
        width:170px;
    }
}

.album--card-fav-section {
    margin-right: 20px;
    margin-top: 20px;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px; 
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    border-left: white;
    height: 102px;  
}

.album--card-fav-section-img {
    padding: 10px;
    width: 100px;
    height: 100px;
}

.album--card-fav-section-img-div {
    border-left: 1px solid #e8e8e8;
    border-top: 1px solid #e8e8e8;
    border-bottom: 1px solid #e8e8e8;        
    margin-top: 20px;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;  
    height: 102px;  
    width: 90px; 
    background: rgb(255, 255, 255); /* Old browsers */
}

.album--color-bad { /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgb(255, 83, 83) 0%,rgb(255, 255, 255) 30%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(rgba(255, 83, 83, 0.657)),color-stop(30%, rgb(255, 255, 255)));
    background: -webkit-linear-gradient(left, rgba(255, 83, 83, 0.657) 0%,rgb(255, 255, 255) 30%);
    background: linear-gradient(to right, rgba(255, 83, 83, 0.657) 0%,rgb(255, 255, 255) 30%);    
}

.album--color-decent { /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgb(255, 255, 83) 0%,rgb(255, 255, 255) 30%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 83, 0.657)),color-stop(30%, rgb(255, 255, 255)));
    background: -webkit-linear-gradient(left, rgba(255, 255, 83, 0.657) 0%,rgb(255, 255, 255) 30%);
    background: linear-gradient(to right, rgba(255, 255, 83, 0.657) 0%,rgb(255, 255, 255) 30%);    
}

.album--color-good { /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgb(83, 255, 83) 0%,rgb(255, 255, 255) 30%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(rgba(83, 255, 83, 0.657)),color-stop(30%, rgb(255, 255, 255)));
    background: -webkit-linear-gradient(left, rgba(83, 255, 83, 0.657) 0%,rgb(255, 255, 255) 30%);
    background: linear-gradient(to right, rgba(83, 255, 83, 0.657) 0%,rgb(255, 255, 255) 30%);    
}

.album--card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.album--card:hover .pulse {    
    border-radius: 50%;
    box-shadow: 0 0 0 rgba(5, 56, 107, 0.4);
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
}

.album--font-size-name {
    font-size: 15px;
}

.album--font-size-year {
    font-size: 14px; 
}

.album--img {
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
}

.album--img-modal {
    width: 200px;
    height: 200px;
    margin-bottom: 10px;  
    border: 1px solid black;
}

.album--img-modal-div {
    display: flex;
    justify-content: center;
}

.album--modal-info {
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 20px;
    color: black;
    margin-bottom: 10px; 
}

.album--icon {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.album--icon:hover  {
    -webkit-transform: scale(1.65);
            transform: scale(1.65);
}

.album--icon-close {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.album--star-icon {
    -webkit-transform: scale(3);
            transform: scale(3);
    stroke: #05386B;
    stroke-width: 5;
}

.album--star-wrapper {
    height: 41px;
    width: 41px;
}

.album--stars {
    display: flex;
    justify-content: center;
    padding-top: 10px;
    padding-left: 27px;
}

.album--title {
    padding-top: 5px;
    padding-bottom: 10px;
}
.favourite--album-div {
    padding-top:10px;
}

.favourite--bg-color {    
    border-radius: 5px;
    margin-left: 7.5px;
    margin-right: 7.5px;  
    margin-bottom: 25px;  
    background: rgb(205, 222, 241); /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgb(205, 222, 241) 0%,rgb(183, 209, 240) 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(205, 222, 241)),to(rgb(183, 209, 240)));
    background: linear-gradient(to bottom, rgb(205, 222, 241) 0%,rgb(183, 209, 240) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

.favourite--empty {
    color: #05386B;
    padding-top: 35px;
    padding-right: 17.5px;
    font-weight: bold;
    opacity: 0.75;
}

.favourite--fixed-bg {
    background: #05386B; /* Old browsers */ /* FF3.6-15 */
