    background: linear-gradient(to bottom, #05386B 0%,#395f8b 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    z-index: -2;
    min-height: 100%;
    min-width: 1024px;
    width: 100%;
    height: auto;
    position: fixed;
    top: 0;
    left: 0;
}

.favourite--font-size {
    font-size: 27.5px;
    color: #05386B;
    padding-top: 7.5px;
}

.favourite--group {
    margin-left: 15px;
    padding-bottom: 30px;
}

.favourite--section-color-bad {
    background: rgb(249, 252, 255); /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgb(255, 83, 83) 0%,rgb(249, 252, 255) 30%); /* Chrome10-25,Safari5.1-6 */
