}

.favourite--section-color-decent {
    background: rgb(249, 252, 255); /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgb(255, 255, 83) 0%,rgb(249, 252, 255) 30%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 83, 0.5)),color-stop(30%, rgb(249, 252, 255)));
    background: -webkit-linear-gradient(left, rgba(255, 255, 83, 0.5) 0%,rgb(249, 252, 255) 30%);
    background: linear-gradient(to right, rgba(255, 255, 83, 0.5) 0%,rgb(249, 252, 255) 30%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */  
}

.favourite--section-color-good {
    background: rgb(249, 252, 255); /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgb(83, 255, 83) 0%,rgb(249, 252, 255) 30%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(rgba(83, 255, 83, 0.5)),color-stop(30%, rgb(249, 252, 255)));
    background: -webkit-linear-gradient(left, rgba(83, 255, 83, 0.5) 0%,rgb(249, 252, 255) 30%);
    background: linear-gradient(to right, rgba(83, 255, 83, 0.5) 0%,rgb(249, 252, 255) 30%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */  
}

.favourite--section-name {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    text-align: center;
    border-bottom: 2px #05386B solid
}

.favourite--star-icon {
    font-size: 30px;
    padding-left:5px;
    padding-bottom: 15px;
    padding-top: 15px;
    stroke: #05386B;
