.app--add {
    font-size: 35px;
    position: absolute;
    right: 15px;
    top: 10px;
    stroke: black;
    stroke-width: 25;
}

.app--backtop {
    height: 40px;
    width: 40px;
    line-height: 40px;
    border-radius: 15px;
    background-color:rgb(205, 222, 241); 
    color: #05386B;
    text-align: center;
    font-size: 20px;
    border: 1px solid #05386B
}

.app--backtop:hover {
    background-color: white
}

.app--icon {
    -webkit-animation: big-pulse 2s infinite;
            animation: big-pulse 2s infinite;    
    border-radius: 50%;
}

.app--footer {
    text-align: center;
    padding-top: 75px;
    padding-bottom: 25px;
    color: white;
}

.app--header {
    height: 200px;
    background-image: url(/my-albums/static/media/background.7ce43fc4.png);
    background-position: top center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    min-width: 320px;
    text-align: center;
    padding-top: 30px;
}

.app--logo {
    width: 225px;
}

.font-roboto {
    font-family: 'Roboto', sans-serif;
}

.font-roboto-title {
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}

.font-courier {
    font-family: 'Courier New', Courier, monospace;
}


@-webkit-keyframes pulse { 
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(5, 56, 107, 0.4);
    }
    70% {
        -webkit-box-shadow: 0 0 0 7.5px rgba(5, 56, 107, 0);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(5, 56, 107, 0);
    }
}

@keyframes pulse {
    0% {
      box-shadow: 0 0 0 0 rgba(5, 56, 107, 0.4);
    }
    70% {
        box-shadow: 0 0 0 7.5px rgba(5, 56, 107, 0);
    }
    100% {
        box-shadow: 0 0 0 0 rgba(5, 56, 107, 0);
    }
}

@-webkit-keyframes big-pulse { 
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.4);
    }
    70% {
        -webkit-box-shadow: 0 0 0 20px rgba(255, 255, 255, 0);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
    }
}

@keyframes big-pulse {
    0% {
      box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.4);
    }
    70% {
        box-shadow: 0 0 0 20px rgba(255, 255, 255, 0);
    }
    100% {
        box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
    }
}

@media only screen and (min-width:768px) {
    .app--header {
        height: 350px;
    }    
    .app--logo {
        margin-top: 40px;
        width: 350px;
    }    
    .app--add {
        font-size: 85px;
        right: 75px;
        top: 100px;
    }    
}

